import { useEffect, useState, useRef } from "react";
import { CodeEditor } from "../../components/CodeEditor/codeEditor";
import '../../styles/common.less';
import './customSnippetsList.less';
import piceUrl from "../../images/pice.jpg";
import addUrl from "../../images/add.png"
import DialogCom from "../../components/DiologCom/DialogCom";

export const CustomSnippetsList = (props) => {
    let { snippetsJson } = props
    let snippetsKeys = snippetsJson ? Object.keys(snippetsJson) : []
    let customSnippetsKeys = []
    snippetsKeys.map(snippetKey => {
        if (snippetKey.split("custom_").length > 1) {
            customSnippetsKeys.push({ key: snippetKey, val: snippetsJson[snippetKey] })
        }
    })
    console.log(customSnippetsKeys)
    const [showEditor, setShowEditor] = useState(true)
    const [showAddDialog, setShowAddDialog] = useState(false)
    const [currCode, setCurrCode] = useState("//请输入代码片段")
    const [currKey, setCurrKey] = useState(undefined)


    const childRef = useRef()

    useEffect(() => {

    }, [currCode])

    const editSnippet = (key) => {
        setCurrKey(key)
        setShowEditor(true)
        if (snippetsJson[key]?.body && snippetsJson[key].body.length > 0) {
            let allCode = ""
            snippetsJson[key].body.forEach(code => {
                allCode += code + "\n"
            });
            setCurrCode(allCode)
        } else {
            setCurrCode("//请输入代码片段")
        }
    }

    const deleteSnippet = (key) => {

    }

    const saveSnippet = () => {
        let key = currKey
        console.log(key)
        if (snippetsJson[key]?.body) {
            snippetsJson[key].body = currCode.split("\n")
        }

        setShowEditor(false)
    }

    const updateCode = (code) => {
        setCurrCode(code)
    }

    const addSnippetItem = () => {
        setShowAddDialog(true)
    }

    // const saveCode = (key, codeArr) => {
    //     snippetsJson[key].body = codeArr
    // }

    const listItems = customSnippetsKeys.map((item, idx) => {
        return (
            <div className="conlist snippetsItem" key={idx}>
                <div className="spImg ">
                    <img src={piceUrl} width="100px" height="100px" />
                </div>
                <div className="">
                    <h6>片段名：{item.key.split('custom_')[1]}</h6>
                    <p>快捷命令：{item.val.prefix}</p>
                    <div className="spItemBtns">
                        <button onClick={() => { editSnippet(item.key) }} className="editBtn" >编辑</button>
                        <button onClick={() => { deleteSnippet(item.key) }} className="deleteBtn" role="button">删除</button>
                    </div>
                    <div className="snippetDescribe">
                        {item?.val?.description ? item.val.description : "通用片段描述"}
                    </div>
                </div>
            </div>
        )
    });

    console.log(showEditor)
    return (
        <div className="snippetsList">
            {(!showEditor && customSnippetsKeys.length > 0) &&
                <div>
                    <h3>自定义片段</h3>
                    <p className="desc">用户自定义代码片段</p>
                    <div className="con">
                        <div className="conlist addSnippetBtn" onClick={() => { addSnippetItem() }}>
                            <div><img src={addUrl} /></div>
                        </div>
                        {listItems}
                    </div>
                </div>
            }
            {showEditor &&
                <div>
                    <CodeEditor currCode={currCode} updateCode={updateCode} />
                    <button className="saveSnippetsBtn" onClick={() => { saveSnippet() }}>保存</button>
                </div>
            }
            <DialogCom btnTxt={["取消", "确定"]} cRef={childRef} >
                <p>名称：</p>
                <input type="text" />
                <p>描述：</p>
                <input type="text" />
                <p>快捷命令：</p>
                <input type="text" />
            </DialogCom>
        </div>
    )

}